import React, { useState, useEffect } from 'react'
import axios from 'axios'
import {Link} from "react-router-dom";
import BungieApi from '../helpers/BungieApi';

export default function EntryList(props) {
    const bungieApi = new BungieApi();

    const defaultEntryList = {
        list: {},
        loading: true,
    };

    const [entryList, setEntryList] = useState(defaultEntryList);

    useEffect(() => {
        const getEntryList = async () => {

            const {data} = await axios(bungieApi.getDataUrl(props.list));

            const sortedList = data.sort( (a, b) => a.index - b.index );
            // Update state
            setEntryList({list: sortedList, loading: false})
        };

        getEntryList();
    }, []);


    return  entryList.loading ? (
        <progress className="progress is-small is-primary" max="100"/>
    ) : (
             entryList.list.map( (e)=>
                <Link className="panel-block" key={e.name} to={props.baseNav + "/" + e.name}>{e.title}</Link>)
         );
}