import React from 'react';
import { Route } from 'react-router-dom';
import BungieApi from '../helpers/BungieApi';
import EntryList from "./EntryList";
import Lore from "./Lore";
import ActivityItem from'./ActivityItem';
import ActivityModifierItem from './ActivityModifierItem';
import InventoryItem from './InventoryItem';
import Collectible from './Collectible';
import SocketCategory from './SocketCategory';
import Destination from './Destination';
import NodeStepSummary from './NodeStepSummary';
import ActivityMode from './ActivityMode';

export default function DataBrowser(props) {
    const bungieApi = new BungieApi();
    const manifestData = bungieApi.getManifestData();

    return (
        <div className="container columns">
            {
                manifestData.map( (data,index) => (
                    <Route key={data.path} path={data.path}>
                        <div className="column is-2 is-narrow-mobile is-fullheight has-background-grey-light">
                            <nav className="panel is-dark is-centered is-radiusless">
                            <p className="panel-heading is-radiusless">{data.title}</p>
                                <div className="dl-scrollable-nav">
                                    <EntryList list={data.folder} baseNav={data.path}/>
                                </div>
                            </nav>
                        </div>
                    </Route>
                ))
            }
            <div className="column is-10 container has-background-black-bis is-dark has-text-white">
                <Route path="/db/lore/:id" component={Lore}/>
                <Route path="/db/activity/:id" component={ActivityItem}/>
                <Route path="/db/activitymodifier/:id" component={ActivityModifierItem}/>
                <Route path="/db/activitymode/:id" component={ActivityMode}/>
                <Route path="/db/inventoryitem/:id" component={InventoryItem}/>
                <Route path="/db/collectible/:id" component={Collectible}/>
                <Route path="/db/socketcategory/:id" component={SocketCategory}/>
                <Route path="/db/destination/:id" component={Destination}/>
                <Route path="/db/nodestepsummary/:id" component={NodeStepSummary}/>
            </div>
        </div>
    );
}